import "../bootstrap";
// --- Post bootstrap -----
import React, { Component } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { connect } from "react-redux";
import { ACTION_TYPES } from "../modules/constants";

import regression from "regression";

import Button from "@material-ui/core/Button";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";

import compose from "../modules/utils/compose";
import { isEmpty } from "../modules/utils/object";

import AppContent from "../modules/components/AppContent";

const styles = theme => ({
  root: {
    marginBottom: 100
  },
  content: {
    marginTop: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2,
    padding: (0, theme.spacing.unit * 2)
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 200
  },
  button: {
    margin: theme.spacing.unit
  },
  table: {
    minWidth: 700
  }
});

class RegressionPage extends Component {
  state = {
    itemX: null,
    itemY: null,
    rows: []
  };

  handleChange = name => event => {
    this.setState({ [name]: Number(event.target.value) });
  };

  handleAddRow = () => {
    const row = [this.state.itemX, this.state.itemY];
    this.setState({
      rows: [...this.state.rows, row]
    });
  };

  handleClear = () => {
    this.setState({
      rows: []
    });
  };

  validRow = () => {
    const { itemX, itemY } = this.state;
    return itemX !== null && itemY !== null;
  };

  rSqure = (rows, result) => {
    const offset = result.equation[1];
    const slope = result.equation[0];
    let sum = 0,
      sse = 0,
      ssyy = 0,
      mean = 0;
    let i;
    const length = rows.length;
    for (i = 0; i < length; i++) {
      sum += rows[i][1];
    }
    mean = sum / length;
    for (i = 0; i < length; i++) {
      const best = offset + slope * rows[i][0];
      ssyy += Math.pow(rows[i][1] - mean, 2);
      sse += Math.pow(rows[i][1] - best, 2);
    }
    if (ssyy !== 0) {
      return 1 - sse / ssyy;
    }

    return 0;
  };

  dispatchAbout = about => {
    this.props.dispatch({
      type: ACTION_TYPES.ABOUT_CHANGE,
      payload: {
        about
      }
    });
  };

  componentDidUpdate(prevProps, prevState) {
    const { reduxAbout } = prevProps;
    if (!isEmpty(reduxAbout)) {
      this.dispatchAbout({});
    }
  }

  componentDidMount() {
    this.setState({
      rows: []
    });
  }

  render() {
    const { classes } = this.props;
    const { itemX, itemY, rows } = this.state;
    const result =
      rows.length > 1 ? regression.linear(rows, { precision: 16 }) : null;
    const offset = result ? result.equation[1].toPrecision(6) : null;
    const slope = result ? result.equation[0].toPrecision(6) : null;
    const rSq = result ? this.rSqure(rows, result).toPrecision(6) : 0;
    return (
      <AppContent className={classes.root} title="Linear Regression">
        <div className={classes.content}>
          <Typography variant="h4" gutterBottom>
            Input Values
          </Typography>
          <p />
          <TextField
            id="new-item-x"
            key="new-item-x"
            label="Measured, x"
            className={classes.textField}
            type="number"
            value={itemX}
            onChange={this.handleChange("itemX")}
            margin="normal"
          />
          <TextField
            id="new-item-y"
            key="new-item-y"
            label="True, y"
            className={classes.textField}
            type="number"
            value={itemY}
            onChange={this.handleChange("itemY")}
            margin="normal"
          />
          <Button
            variant="contained"
            className={classes.button}
            onClick={this.handleAddRow}
            disabled={!this.validRow()}
          >
            Add
          </Button>
          <Button
            variant="contained"
            className={classes.button}
            onClick={this.handleClear}
            disabled={rows.length < 1}
          >
            Clear List
          </Button>
          <p />
          <Typography variant="h4" gutterBottom>
            Parameters
          </Typography>
          <p />
          <TextField
            disabled
            id="result-offset"
            key="result-offset"
            label="Offset"
            className={classes.textField}
            value={offset}
            margin="normal"
            InputLabelProps={{ shrink: true }}
          />
          <TextField
            disabled
            id="result-slope"
            key="result-slope"
            label="Slope"
            className={classes.textField}
            value={slope}
            margin="normal"
            InputLabelProps={{ shrink: true }}
          />
          <p />
          <Typography variant="h4" gutterBottom>
            Statistics
          </Typography>
          <TextField
            disabled
            id="result-Ns"
            key="result-Ns"
            label="Number of Points"
            className={classes.textField}
            value={rows.length}
            margin="normal"
            InputLabelProps={{ shrink: true }}
          />
          <TextField
            disabled
            id="result-rsq"
            key="result-rsq"
            label="R-Square(COD)"
            className={classes.textField}
            value={rSq}
            margin="normal"
            InputLabelProps={{ shrink: true }}
          />
          <p />
          <Typography variant="h4" gutterBottom>
            Input Data
          </Typography>
          <p />
          <Paper>
            <Table className={classes.table}>
              <TableHead>
                <TableRow>
                  <TableCell>Pt</TableCell>
                  <TableCell>X</TableCell>
                  <TableCell>Y</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map((row, index) => (
                  <TableRow key={index.toString()}>
                    <TableCell>{index}</TableCell>
                    <TableCell>{row[0]}</TableCell>
                    <TableCell>{row[1]}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </Paper>
        </div>
      </AppContent>
    );
  }
}

RegressionPage.propTypes = {
  classes: PropTypes.object.isRequired,
  dispatch: PropTypes.func.isRequired,
  reduxAbout: PropTypes.object.isRequired
};

export default compose(
  connect(state => ({
    reduxAbout: state.about
  })),
  withStyles(styles)
)(RegressionPage);
